html {
    box-sizing: border-box;
    overflow: -moz-scrollbars-vertical;
    overflow-y: scroll;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0;
    background: #fafafa;
}
.swagger-ui .topbar {
    border-bottom: 1px solid #bebebe;
}

.topbar-wrapper {
    display: flex;
}

.topbar-wrapper .topbar-title {
    display: flex;
    flex: 3 1;
}

.topbar-wrapper .topbar-title img {
    max-width: 300px;
    max-height: 65px;
    align-self: center;
}

.topbar-wrapper .topbar-title .topbar-title-info {
    font-family: 'Helvetica Neue', 'Tahoma', 'sans-serif', Helvetica, Arial;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    justify-content: center;
}

.topbar-wrapper .topbar-title .topbar-title-info h1 {
    margin: 0;
    font-size: 16pt;
}

.topbar-wrapper .topbar-title .topbar-title-info span {
    font-size: 11pt;
}

.topbar-wrapper .topbar-selector {
    flex: 1 1;
}

.topbar-wrapper .topbar-selector #select {
    width: 100%;
}
